<script>
import axios from 'axios'
export default {
    name: 'ServerWorking',
    data() {
      return {
          serverAddress: 'https://studentmgr.daren.black/api',
          dialog: false,
          dialogTite: '',
          dialogContent: '',
          dialogBehave: '',
          dialogButtonText: '',
          error: {
            orgError: false,
            personalError: false,
            homeError: false,
            homeMemberError: false
          },
          studentInfo: {
            orgInfo: {
              year: '',
              major: '',
              class: '',
              orgTpye: '',
              lengthOfSchool: ''
            },
            personalInfo: {
              name: '',
              gender: '',
              birth: '',
              photo: '',
              inSchoolTime: '',
              domNumber: '',
              roomNumber: '',
              studentID: '',
              exName: '',
              nation: '',
              politicalStatus: '',
              from: '',
              homeTown: '',
              graduated: '',
              citizenID: '',
              health: '',
              religious: '',
              habit: '',
              phone1: '',
              phone2: '',
              resume: ''
            },
            familyInfo: {
              income: '',
              address: '',
              parentsPhone: '',
              religiousTime: '',
              religiousFreq: ''
            },
            familyMember1: {
              relation: '',
              name: '',
              age: '',
              politicalStatus: '',
              job: '',
              health: ''
            },
            familyMember2: {
              relation: '',
              name: '',
              age: '',
              politicalStatus: '',
              job: '',
              health: ''
            },
            familyMember3: {
              relation: '',
              name: '',
              age: '',
              politicalStatus: '',
              job: '',
              health: ''
            }
          },
          orgItems: []
      }
    },
    methods: {
      alert(title, content, buttonText, action) {
        this.dialogTite = title;
        this.dialogContent = content;
        this.dialogBehave = action;
        this.dialogButtonText = buttonText;
        this.dialog = true;
      },
      confrim() {
        this.alert('确认', '确定要提交吗? 请确认相关信息填写正确', '确定', () => {this.dialog = false; this.submit()});
      },
      submit() {
        try {
          axios({
            method: 'post',
            url: `${this.serverAddress}/students/new`,
            data: {
              studentInfo: this.studentInfo
            }
          }).then(() => {this.$router.push('submitted');})
          
        } catch (error) {
          this.alert('错误', error.message, '确定', () => {this.dialog = false;});
        }
      },
      // checkFrom() {
      //   if(this.studentInfo.orgInfo.year.length === 0){
      //     this.error.orgError = true;
      //   } else {
      //     this.error.orgError = false;
      //   }
    
    },
    created() {
      axios.get(`${this.serverAddress}/status`)
      .then((res) => {
        if(res.data.status === 0) {
          res.data.orgs.forEach(element => {
            let label = `${element.major}, ${element.class}`;
            let item = {
              label,
              orgInfo: element
            }
            this.orgItems.push(item)
          });
        }
      })
      .catch((error) => {
          this.alert('错误', error.message, '确定', () => {this.dialog = false;});
      })
    }
}
</script>

